<template>
  <v-container fluid>
    <v-row class="centered-row">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-card-text>
            <v-form>
              <v-text-field label="Old password" name="oldpassword"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="oldpassword" :error="opError" clearable
              />
              <v-text-field label="New password" name="newpassword"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="newpassword" :error="npError" clearable
              />
              <v-text-field label="Re-type new password" name="cnfpassword"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="cnfpassword" :error="npError" clearable
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" @click="$router.go(-1)"
              :disabled="loading"
            >Cancel</v-btn>
            <v-btn color="primary" @click="okClick"
              :loading="loading" :disabled="!inputOK"
            >Ok</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { chpass } from "@/lib/auth"

export default {
  name: "ChPass",
  data: () => ({
    oldpassword: "",
    newpassword: "",
    cnfpassword: "",
    opError: false,
    npError: false,
    loading: false
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.oldpassword.length) return false
        if (0 === this.newpassword.length) return false
        if (0 === this.cnfpassword.length) return false
        if (this.newpassword !== this.cnfpassword) {
          // eslint-disable-next-line
          this.npError = true
          return false
        } else {
          // eslint-disable-next-line
          this.npError = false
        }
        if (this.newpassword === this.oldpassword) return false
        return true
      } catch {
        return false
      }
    }
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Change Password",backButton:true})
  },
  methods: {
    okClick: async function() {
      this.opError = false
      this.npError = false
      this.loading = true
      this.$store.commit('SET_LOADING', true)
      let resp = await chpass(this.oldpassword, this.newpassword)
      this.$store.commit('SET_LOADING', false)
      this.loading = false
      if (200 === resp.code) {
        let payload = {
          text: "Password has been changed"
        }
        this.$store.commit("SHOW_SNACKBAR", payload)
        this.$router.go(-1)
      } else {
        this.$store.commit("SHOW_APP_ALERT", {
          title: "Change Password",
          text: `Can not change your password! (CODE: ${resp.code})`
        })
      }
    }
  }
}
</script>
